.get_to_know{
    text-align: center;
    padding-top: 10rem;
    padding-bottom: 2rem;
    /* height: 100%; */
    /* position: relative; */
}

.about {
    padding-top: 4rem;
}

.about__container {
    display: grid;
    grid-template-columns: 10% 80%;
    gap: 15%;
    /* height: 100vh; */
}

.about__me {
    width: 250px;
    height: 350px;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    width: 250px;
    height: 350px;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(8deg);
    transition: var(--transition) ;
}
.about__me-image:hover {
    transform: rotate(0);
}

.about__cards { 
    display: grid;
    grid-template-columns: 2fr 3fr 1fr;
    /* grid-template-columns: repeat(3, 2fr); */
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.9rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light)
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light)
}

h2{
    color: var(--color-primary);
    /* margin-bottom: 3rem; */
}

/*            Large device           */
@media screen and (max-width: 1450px) { 
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me {
        width: 250px;
        height: 350px;
        margin: 2rem auto 4rem;
    }
    .get_to_know{
        text-align: center;
        padding-top: 5rem;
        padding-bottom: 2rem;
        /* height: 100%; */
        /* position: relative; */
    }

}

/*            Medium device           */
@media screen and (max-width: 1024px) { 
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me {
        width: 250px;
        height: 350px;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 4rem 0 2.6rem;
        color: var(--color-light)
    }
    .get_to_know{
        text-align: center;
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
    
}

/*            Small device           */
@media screen and (max-width: 600px) { 
    .about{
        height: 150vh;
    }
    .container__pad{
        margin-top: 60vh;
    }
    .about__container {
        display: grid;
        grid-template-rows: 45% 65%;
        height: 100%;
    }
    .about__me{
        width: 250px;
        height: 350px;
        margin: 0 auto 3rem ;
    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        /* margin-bottom: 10rem; */
    }
    .about__content {
        text-align: center;
    }
    .about__content h3 {
        font-size: 12px;
    }
    .about__content small {
        font-size: 9px;
    }
    .about__content p {
        /* margin: 4rem 0 2.6rem; */
        color: var(--color-light)
    }
    .get_to_know{
        text-align: center;
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
    

}

