@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');


* { /* "*" This is a techniquw called a CSS reset, */
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    /* 
        ff6700 
        ebebeb
        c0c0c0
        3a6ea5
        004e98
    */
    /* --color-bg: #c0c0c0;
    --color-bg-variant: #ebebeb;
    --color-primary: #ff6700;
    --color-primary-variant: egb(77,181,255,0.4);
    --color-white: #ff6700;
    --color-light: rgb(255,255,255,0.6); */

    
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: egb(77,181,255,0.4);
    --color-white: #fff;
    --color-light: rgb(255,255,255,0.6);
   
    --transition: all 400ms ease;

    --container-width-lg:70%;
    --container-width-md:90%;
    --container-width-sm:90%;

    --container-hight-lg:50%;
    --container-hight-md:80%;
    --container-hight-sm:100%;

}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: "Poppins", sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assests/some.png); */
}


/* ======================== GENERAL STYLES ======================== */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 5rem;
    height: var(--container-hight-lg);

}

/* section {
    margin-top: 5rem;
    height: 100vh;
} */

section > h2, section > h1 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

section > h5 {
    text-align: center;
}

.text-light {
    color: var(--color-light)
}

a {
    color: var(--color-primary);
    transition: var(--transitaion);
}

a:hover {
   color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transitaion);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}



/* ======================== MEDIA QUERIES (MEDIUM DEVICES) ======================== */
@media screen and (max-width:1024px){
    header {
        height: 100vh;
    }
    .container {
        width: var(--container-width-md);
        /* background-color: red; */
        padding-bottom: 5rem;
        /* margin: 5rem; */
    }
}

/* ======================== MEDIA QUERIES (SMALL DEVICES) ======================== */
@media screen and (max-width:600px){
    .container {
        width: var(--container-width-sm);
        /* height: var(--container-hight-sm); */
    }

    section > h2 {
        margin-top: 2rem;
    }
}